import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnSearch extends Component {
  states = '';

  render() {
    return (
      <Button
        {...this.props}
        className={`btnAction btnSearch ${this.props.className}`}
        icon="search"
        size="large"
      >
        {/* {this.props.title} */}
      </Button>
    );
  }
}

BtnSearch.defaultProps = {
  className: '',
  title: 'Tìm kiếm',
};



