import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnUpload extends Component {
  static defaultProps = {
    title: 'Tải lên',
    className: '',
  };
  states = '';
  render() {
    return (
      <Button
        {...this.props}
        className={`btnAction ${this.props.className}`}
        icon='upload'
        size='large'
      >
        {this.props.title}
      </Button>
    );
  }
}



