import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnLock extends Component {
  states = '';

  render() {
    return (
      <Button
        {...this.props}
        className={`btnAction btnLock ${this.props.className}`}
        icon="lock"
      >
        {this.props.title}
      </Button>
    );
  }
}

BtnLock.defaultProps = {
  className: '',
  title: 'Khóa',
};



