import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnClear extends Component {
  states = '';

  render() {
    return (
      <Button
        {...this.props}
        className={`btnAction btnClear ${this.props.className}`}
        // icon="delete"
        size="large"
      >
        {/* {this.props.title} */}
        <img width="20px" alt="" src="/resources/images/btnClear.png" />
      </Button>
    );
  }
}

BtnClear.defaultProps = {
  className: '',
  title: 'Clear',
};



