import { request } from 'utils';

const prefix = '/api/categories';

export const getSubjectTypeById = id =>
  request({
    prefix,
    url: '/SubjectType/GetById',
    method: 'GET',
    params: { id },
    data: {},
  });

export const getSubjectTypes = ({ page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    prefix,
    url: '/SubjectType',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createSubjectTypeById = ({ label }) =>
  request({
    prefix,
    url: '/SubjectType',
    method: 'POST',
    params: {},
    data: { label },
  });

export const updateSubjectTypeById = ({ id, label }) =>
  request({
    prefix,
    url: '/SubjectType',
    method: 'PUT',
    params: {},
    data: { id, label },
  });

export const deleteSubjectType = id =>
  request({
    prefix,
    url: `/SubjectType`,
    method: 'DELETE',
    data: { id },
  });



