import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnView extends Component {
  states = '';

  render() {
    return (
      <Button
        {...this.props}
        className={`btnAction btnView ${this.props.className}`}
        icon="eye"
        size="small"
      >
        {this.props.title}
      </Button>
    );
  }
}

BtnView.defaultProps = {
  className: '',
  title: '',
};



