import React, { useState, useEffect } from 'react';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, message } from 'antd';
import { requiredRule } from 'utils/validateRules';
import {
  getSubjectTypeById,
  createSubjectTypeById,
  updateSubjectTypeById,
} from 'services/subjectTypeService';
import { getQueryParamId } from 'utils/urlUtil';

const FormSubjectType = props => {
  const [initialValue, setInitialValue] = useState({});
  const [loading, setLoading] = useState(false);

  const customGetSubjectTypeById = async id => {
    const res = await getSubjectTypeById(id);
    setInitialValue(res.data);
  };

  useEffect(() => {
    const { subjectTypeId } = getQueryParamId();
    if (subjectTypeId) {
      customGetSubjectTypeById(subjectTypeId);
    }
  }, []);

  const returnToList = () => {
    props.history.goBack();
  };

  const saveSubjectType = async subjectType => {
    const { id } = initialValue;
    subjectType.id = id;
    try {
      setLoading(true);
      if (!id) {
        await createSubjectTypeById(subjectType);
      } else {
        await updateSubjectTypeById(subjectType);
      }
      message.success('Lưu thành công');
      returnToList();
    } catch (err) {
      message.error('Lưu không thành công');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (!err) {
        saveSubjectType(values);
      }
    });
  };

  const { getFieldDecorator } = props.form;

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
    colon: false,
  };

  return (
    <Card title={initialValue.id ? 'Cập nhật XL chứng chỉ' : 'Thêm danh mục XL chứng chỉ'}>
      <Row>
        <Form {...formItemLayout} onSubmit={handleSubmit} className="login-form">
          <Form.Item hasFeedback label="Loại môn học" {...formItemLayout}>
            {getFieldDecorator('label', {
              initialValue: initialValue.label,
              rules: [requiredRule],
            })(<Input />)}
          </Form.Item>
          <Form.Item {...formItemLayout} label=" ">
            <BtnSave loading={loading} htmlType="submit" />
            <BtnCancel onClick={() => returnToList()} />
          </Form.Item>
        </Form>
      </Row>
    </Card>
  );
};

export default Form.create()(FormSubjectType);



