import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class Btn extends Component {
  states = '';

  render() {
    return (
      <Button {...this.props} className={`btnAction ${this.props.className}`} size="large">
        {this.props.title}
        {this.props.children}
      </Button>
    );
  }
}

Btn.defaultProps = {
  className: '',
  title: '',
};



