import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnExport extends Component {
  static defaultProps = {
    filetype: 'excel',
    title: 'Xuất Excel',
    className: '',
  };
  states = '';
  render() {
    const { filetype } = this.props;
    return (
      <Button
        {...this.props}
        className={`btnAction ${this.props.className}`}
        icon={filetype === 'excel' ? 'file-excel' : 'file'}
        size="large"
      >
        {this.props.title}
      </Button>
    );
  }
}



