import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnExport extends Component {
  static defaultProps = {
    filetype: 'pdf',
    title: 'Xuất Pdf',
    className: '',
  };
  states = '';
  render() {
    return (
      <Button
        {...this.props}
        className={`btnAction ${this.props.className}`}
        icon="file-pdf"
        size="large"
      >
        {this.props.title}
      </Button>
    );
  }
}



